import { FormManager } from '../Common/FormManager';
import { onDocReady } from '../Common/UiHelper';

export class LeadApprovalManager {

    async Init(): Promise<void> {
        let submitButton = document.getElementById("submitButton") as HTMLButtonElement;
        if (submitButton !== null) {
            submitButton.addEventListener("click", (e: Event) => this.Save());
        }
    }

    async Save(): Promise<void> {
        let formManager = new FormManager('leadApprovalForm');
        if (await formManager.ValidateForm()) {
            let url = `${window.location.href}`;
            let response = await formManager.SubmitForm(url);
            if (response.ok) {
                window.location.reload();
            } else {
                await formManager.ShowSuccessFailureMessage(response);
            }
        }
    }
}
onDocReady(async () => {
    let page = new LeadApprovalManager();
    await page.Init();
});